import request from '@/utils/request'
/**
 * @description: 
 * @param {type} 
 * @return: Promise
 */
export function login(data) {
  return request({
    url: '/user/sign/verifypassword',
    method: 'post',
    data
  })
}

/**
 * @description:  token || uid
 * @param {type} 
 * @return: Promise
 */
export function getuser(uid) {
  return request({
    url: '/user/info/getuser',
    method: 'get',
    params: { uid }
  })
}

/**
 * @description: 
 * @param {type} 
 * @return: Promise
 */
export function logout() {
  return request({
    url: '/user/sign/out',
    method: 'post'
  })
}

