import request from '@/utils/request'

/**
 * @description: 
 * @param {type} 
 * @return: 
 */

export function materialAdd(data) {
    return request({
      url: '/subject/material/add',
      method: 'post',
      data
    })
}

/**
 * @description: 
 * @param {type} 
 * @return: 
 */

export function materialModify(data) {
    return request({
      url: '/subject/material/modify',
      method: 'post',
      data
    })
}

/**
 * @description: 
 * @param {type} 
 * @return: 
 */

export function materialRemove(data) {
    return request({
      url: '/subject/material/remove',
      method: 'post',
      data
    })
}



/**
 * @description:   sid
 * @param {type} 
 * @return: Promise
 */

export function materialMineList(data) {
  return request({
    url: '/subject/material/mine',
    method: 'get',
    params:data
  })
}

/**
 * @description: 
 * @param {type} 
 * @return: Promise
 */

export function materialGet(data) {
  return request({
    url: '/subject/material/get',
    method: 'get',
    params: data
  })
}

