import request from '@/utils/request'

/**
 * @description: 
 * @param {page,limit,status} 
 * @return: Promise
 */
export function list(data) {
    return request({
      url: '/user/message/list',
      method: 'get',
      params:data
    })
}

/**
 * @description: 
 * @param {page,limit} 
 * @return: Promise
 */
export function notice(data) {
    return request({
      url: '/user/message/notice',
      method: 'get',
      params:data
    })
}  


/**
 * @description: 
 * @param {id} 
 * @return: Promise
 */
export function remove(data) {
    return request({
      url: '/user/message/remove',
      method: 'post',
      data
    })
}

/**
 * @description: 
 * @param {id} 
 * @return: Promise
 */
export function setread(data) {
    return request({
      url: '/user/message/setread',
      method: 'post',
      data
    })
}



/**
 * @description: 
 * @param {id} 
 * @return: Promise
 */
export function hasnew(data) {
    return request({
      url: '/user/message/hasnew',
      method: 'get',
      params: data
    })
}